<template>
	<div
		style="
			padding-top: 85px;
			display: flex;
			min-height: 40vh;
			flex-direction: column;
		">
		<div class="title">Üye Ol</div>
		<div>
			<input type="text" name="" id="" v-model="cusName" placeholder="İsim" />
		</div>
		<div>
			<input
				type="text"
				name=""
				id=""
				v-model="cusSurName"
				placeholder="Soy İsim" />
		</div>
		<div>
			<input
				type="text"
				name=""
				id=""
				v-model="cusInfos.cusAdress"
				placeholder="Açık Adres" />
		</div>
		<div>
			<input
				type="text"
				name=""
				id=""
				v-model="cusInfos.cusProvince"
				placeholder="İl" />
		</div>
		<div>
			<input
				type="text"
				name=""
				id=""
				v-model="cusInfos.cusDist"
				placeholder="İlçe" />
		</div>
		<div>
			<input
				type="password"
				name=""
				id=""
				v-model="cusInfos.cussPass"
				placeholder="Şifre" />
			<input
				type="password"
				name=""
				id=""
				v-model="cusRePass"
				placeholder="Şifre Tekrar" />
		</div>
		<div>
			<input
				type="text"
				name=""
				id=""
				v-model="cusInfos.cusEmail"
				placeholder="E-Mail" />
		</div>
		<button @click="signIn">Kayıt Ol</button>
	</div>
</template>
<style scoped>
	.title {
		color: white;
	}
</style>
<script>
	export default {
		data() {
			return {
				cusName: "",
				cusSurName: "",
				cusRePass: "",
				cusInfos: {
					cusNameSurName: "",
					cusAdress: "",
					cusProvince: "",
					cusDist: "",
					cussPass: "",
					cusEmail: "",
				},
			};
		},
		methods: {
			signIn() {
				this.$store.dispatch("signIn/getAuthentication", this.cusInfos);
			},
		},
		watch: {
			cusname() {
				this.cusInfos.cusNameSurName = this.cusName + this.cusSurName;
			},
			cusSurName() {
				this.cusInfos.cusNameSurName = this.cusName + this.cusSurName;
			},
		},
	};
</script>
